/* ===== Global Professional Blue Theme Styles ===== */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

/* CSS Variables for Blue Theme */
:root {
  --primary-blue: #2563eb;
  --primary-blue-dark: #1d4ed8;
  --primary-blue-light: #3b82f6;
  --secondary-blue: #1e40af;
  --accent-blue: #60a5fa;
  --light-blue: #dbeafe;
  --dark-blue: #1e3a8a;
  --gradient-blue: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  --gradient-blue-light: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  --shadow-blue: 0 4px 14px 0 rgba(37, 99, 235, 0.15);
  --shadow-blue-hover: 0 8px 25px 0 rgba(37, 99, 235, 0.25);
  --border-radius: 12px;
  --border-radius-sm: 8px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Global Font */
* {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* ===== Professional Table Styles ===== */
.professional-table {
  background: #ffffff;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-blue);
  overflow: hidden;
  border: none;
  margin: 20px 0;
  transition: var(--transition);
}

.professional-table:hover {
  box-shadow: var(--shadow-blue-hover);
  transform: translateY(-2px);
}

.professional-table .table {
  margin-bottom: 0;
  border-collapse: separate;
  border-spacing: 0;
}

.professional-table .table thead th {
  background: linear-gradient(135deg, rgb(36, 35, 123) 0%, rgb(36, 35, 123) 100%);
  color: #ffffff;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  padding: 20px 16px;
  border: none;
  position: relative;
}

.professional-table .table thead th:first-child {
  border-top-left-radius: var(--border-radius);
}

.professional-table .table thead th:last-child {
  border-top-right-radius: var(--border-radius);
}

.professional-table .table thead th::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: rgba(255, 255, 255, 0.2);
}

.professional-table .table tbody tr {
  transition: var(--transition);
  border: none;
}

.professional-table .table tbody tr:hover {
  background: var(--light-blue);
  transform: scale(1.01);
}

.professional-table .table tbody tr:nth-child(even) {
  background: #f8fafc;
}

.professional-table .table tbody tr:nth-child(even):hover {
  background: var(--light-blue);
}

.professional-table .table tbody td {
  padding: 16px;
  border: none;
  border-bottom: 1px solid #e2e8f0;
  font-size: 14px;
  color: #374151;
  vertical-align: middle;
}

.professional-table .table tbody tr:last-child td {
  border-bottom: none;
}

/* Action buttons in tables */
.table-action-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: var(--border-radius-sm);
  border: none;
  background: var(--gradient-blue-light);
  color: #ffffff;
  font-size: 14px;
  transition: var(--transition);
  margin: 0 4px;
  text-decoration: none;
}

.table-action-btn:hover {
  background: var(--primary-blue-dark);
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: var(--shadow-blue);
}

.table-action-btn.danger {
  background: linear-gradient(135deg, #dc2626, #b91c1c);
}

.table-action-btn.danger:hover {
  background: linear-gradient(135deg, #b91c1c, #991b1b);
}

.table-action-btn.success {
  background: linear-gradient(135deg, #059669, #047857);
}

.table-action-btn.success:hover {
  background: linear-gradient(135deg, #047857, #065f46);
}

/* ===== Professional Form Styles ===== */
.professional-form {
  background: #ffffff;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-blue);
  padding: 32px;
  margin: 20px 0;
  border: none;
  transition: var(--transition);
}

.professional-form:hover {
  box-shadow: var(--shadow-blue-hover);
}

.professional-form .form-header {
  background: linear-gradient(135deg, rgb(36, 35, 123) 0%, rgb(36, 35, 123) 100%);
  color: #ffffff;
  padding: 24px 32px;
  margin: -32px -32px 32px -32px;
  border-radius: var(--border-radius) var(--border-radius) 0 0;
  position: relative;
}

.professional-form .form-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: rgba(255, 255, 255, 0.3);
}

.professional-form .form-header h3 {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 12px;
}

.professional-form .form-header .form-icon {
  width: 32px;
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}

.form-group {
  margin-bottom: 24px;
}

.form-label {
  display: block;
  font-weight: 600;
  color: #374151;
  margin-bottom: 8px;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-control {
  width: 100%;
  padding: 14px 16px;
  border: 2px solid #e5e7eb;
  border-radius: var(--border-radius-sm);
  font-size: 16px;
  transition: var(--transition);
  background: #ffffff;
  color: #374151;
  min-height: 48px;
  line-height: 1.5;
}

.form-control:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
  background: #ffffff;
}

.form-control::placeholder {
  color: #9ca3af;
  font-style: italic;
}

.form-control:disabled {
  background: #f9fafb;
  color: #6b7280;
  cursor: not-allowed;
}

/* Enhanced Select dropdown styling */
select.form-control {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='m6 8 4 4 4-4'/%3e%3c/svg%3e");
  background-position: right 12px center;
  background-repeat: no-repeat;
  background-size: 16px;
  padding-right: 40px;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
}

/* Fix for select option text cropping */
select.form-control option {
  padding: 12px 16px;
  font-size: 16px;
  color: #374151;
  background-color: #ffffff;
  border: none;
  line-height: 1.5;
  white-space: normal;
  word-wrap: break-word;
  min-height: 2.5rem;
  display: block;
}

select.form-control option:hover {
  background-color: var(--light-blue);
  color: var(--primary-blue-dark);
}

select.form-control option:checked,
select.form-control option:selected {
  background-color: var(--primary-blue);
  color: #ffffff;
}

select.form-control option:focus {
  background-color: var(--light-blue);
  color: var(--primary-blue-dark);
}

/* Additional fixes for select option text cropping across browsers */
select.form-control {
  overflow: visible;
  text-overflow: ellipsis;
}

select.form-control option {
  overflow: visible;
  text-overflow: clip;
  white-space: pre-wrap;
  word-break: break-word;
  max-width: none;
}

/* Ensure proper spacing for long text in options */
select.form-control option[value] {
  padding: 12px 16px;
  margin: 2px 0;
}

/* Fix for Firefox */
@-moz-document url-prefix() {
  select.form-control option {
    padding: 8px 12px;
    line-height: 1.4;
  }
}

/* Fix for Webkit browsers */
@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select.form-control option {
    padding: 10px 14px;
    line-height: 1.5;
  }
}

