/* Textarea styling */
.form-control textarea {
  resize: vertical;
  min-height: 120px;
}

/* File input styling */
.form-control[type="file"] {
  padding: 12px;
  border: 2px dashed #d1d5db;
  background: #f9fafb;
  cursor: pointer;
}

.form-control[type="file"]:hover {
  border-color: var(--primary-blue);
  background: var(--light-blue);
}

/* ===== Professional Button Styles ===== */
.btn-professional {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 14px 28px;
  border: none;
  border-radius: var(--border-radius-sm);
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: var(--transition);
  cursor: pointer;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.btn-professional::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.btn-professional:hover::before {
  left: 100%;
}

.btn-primary-professional {
  background: linear-gradient(135deg, rgb(36, 35, 123) 0%, rgb(36, 35, 123) 100%);
  color: #ffffff;
  box-shadow: var(--shadow-blue);
}

.btn-primary-professional:hover {
  background: linear-gradient(135deg, rgb(36, 35, 123) 0%, rgb(36, 35, 123) 100%);
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: var(--shadow-blue-hover);
}

.btn-secondary-professional {
  background: #ffffff;
  color: var(--primary-blue);
  border: 2px solid var(--primary-blue);
}

.btn-secondary-professional:hover {
  background: var(--primary-blue);
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: var(--shadow-blue);
}

.btn-danger-professional {
  background: linear-gradient(135deg, #dc2626, #b91c1c);
  color: #ffffff;
  box-shadow: 0 4px 14px 0 rgba(220, 38, 38, 0.15);
}

.btn-danger-professional:hover {
  background: linear-gradient(135deg, #b91c1c, #991b1b);
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px 0 rgba(220, 38, 38, 0.25);
}

.btn-success-professional {
  background: linear-gradient(135deg, #059669, #047857);
  color: #ffffff;
  box-shadow: 0 4px 14px 0 rgba(5, 150, 105, 0.15);
}

.btn-success-professional:hover {
  background: linear-gradient(135deg, #047857, #065f46);
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px 0 rgba(5, 150, 105, 0.25);
}

/* ===== Status Badges ===== */
.status-badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.success {
  background: #dcfce7;
  color: #166534;
}

.status-badge.warning {
  background: #fef3c7;
  color: #92400e;
}

.status-badge.error {
  background: #fee2e2;
  color: #991b1b;
}

.status-badge.info {
  background: #dbeafe;
  color: #1e40af;
}

/* ===== Card Styles ===== */
.professional-card {
  background: #ffffff;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-blue);
  border: none;
  overflow: hidden;
  transition: var(--transition);
}

.professional-card:hover {
  box-shadow: var(--shadow-blue-hover);
  transform: translateY(-4px);
}

.professional-card .card-header {
  background: var(--gradient-blue);
  color: #ffffff;
  padding: 20px 24px;
  border: none;
  position: relative;
}

.professional-card .card-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: rgba(255, 255, 255, 0.2);
}

.professional-card .card-body {
  padding: 24px;
}

/* ===== Page Header Styles ===== */
.page-header {
  background: linear-gradient(135deg, rgb(36, 35, 123) 0%, rgb(36, 35, 123) 100%);
  color: #ffffff;
  padding: 32px;
  border-radius: var(--border-radius);
  margin-bottom: 32px;
  position: relative;
  overflow: hidden;
}

.page-header::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  50% { transform: translate(-20px, -20px) rotate(180deg); }
}

.page-header h1 {
  margin: 0;
  font-size: 32px;
  font-weight: 700;
  position: relative;
  z-index: 1;
}

.page-header p {
  margin: 8px 0 0 0;
  opacity: 0.9;
  position: relative;
  z-index: 1;
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  .professional-form {
    padding: 20px;
    margin: 10px 0;
  }
  
  .professional-form .form-header {
    margin: -20px -20px 20px -20px;
    padding: 20px;
  }
  
  .professional-table {
    margin: 10px 0;
  }
  
  .professional-table .table thead th,
  .professional-table .table tbody td {
    padding: 12px 8px;
    font-size: 13px;
  }
  
  .page-header {
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .page-header h1 {
    font-size: 24px;
  }
}

/* ===== Loading States ===== */
.loading-skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* ===== Utility Classes ===== */
.text-blue-primary { color: var(--primary-blue) !important; }
.bg-blue-primary { background-color: var(--primary-blue) !important; }
.bg-blue-gradient { background: var(--gradient-blue) !important; }
.shadow-blue { box-shadow: var(--shadow-blue) !important; }
.border-blue { border-color: var(--primary-blue) !important; }

/* ===== Animation Classes ===== */
.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.slide-in-left {
  animation: slideInLeft 0.5s ease-out;
}

@keyframes slideInLeft {
  from { opacity: 0; transform: translateX(-30px); }
  to { opacity: 1; transform: translateX(0); }
}

.slide-in-right {
  animation: slideInRight 0.5s ease-out;
}

@keyframes slideInRight {
  from { opacity: 0; transform: translateX(30px); }
  to { opacity: 1; transform: translateX(0); }
}

/* ===== Professional Dashboard Cards ===== */
.professional-card {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-blue);
  padding: 1.5rem;
  transition: var(--transition);
  border: 1px solid #e2e8f0;
  height: 100%;
}

.professional-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-blue-hover);
}

.professional-card .card-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.professional-card .card-info {
  flex: 1;
}

.professional-card .card-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-blue-dark);
  margin: 0;
  line-height: 1;
}

.professional-card .card-label {
  font-size: 0.9rem;
  color: #64748b;
  font-weight: 500;
  margin: 0.5rem 0 0 0;
  line-height: 1.2;
}

.professional-card .card-icon {
  background: var(--gradient-blue);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-blue);
  flex-shrink: 0;
