/* Import Global Professional Styles */
@import '../../styles/global.css';

/* ===== Login Page Styles ===== */
.login-container {
  min-height: 100vh;
  /* background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-family: var(--font-family);
}
.mobile-hidden{
  display: none;
}

/* @media (max-width: 680px) {
  .mobile-hidden{
    display: block;
  }
} */
.login-wrapper {
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  max-width: 1000px;
  width: 100%;
  display: flex;
  min-height: 600px;
  animation: slideInUp 0.8s ease-out;
}

/* Left Panel - Brand Section */
.login-left-panel {
  flex: 1;
  background: var(--gradient-blue);
  padding: 60px 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  overflow: hidden;
}

.login-left-panel::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  animation: float 6s ease-in-out infinite;
}

.login-brand {
  z-index: 2;
  position: relative;
}

.brand-logo {
  max-height: 80px;
  margin-bottom: 30px;
  filter: brightness(0) invert(1);
  animation: fadeInLeft 1s ease-out;
}

.brand-title {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 15px;
  animation: fadeInLeft 1s ease-out 0.2s both;
}

.brand-subtitle {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.1rem;
  font-weight: 400;
  animation: fadeInLeft 1s ease-out 0.4s both;
}

.login-illustration {
  z-index: 2;
  position: relative;
  animation: fadeInUp 1s ease-out 0.6s both;
}

.illustration-img {
  width: 100%;
  height: auto;
  max-height: 300px;
  object-fit: contain;
}

/* Right Panel - Form Section */
.login-right-panel {
  flex: 1;
  padding: 60px 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
}

.login-form-container {
  width: 100%;
  max-width: 400px;
}

.login-header {
  text-align: center;
  margin-bottom: 40px;
  animation: fadeInUp 1s ease-out 0.8s both;
}

.login-icon {
  width: 80px;
  height: 80px;
  background: var(--gradient-blue);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  box-shadow: var(--shadow-blue);
  animation: pulse 2s infinite;
}

.login-icon svg {
  font-size: 2rem;
  color: #ffffff;
}

.login-header h3 {
  color: #1f2937;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 10px;
}

.login-header p {
  color: #6b7280;
  font-size: 1rem;
  margin: 0;
}

/* Form Styles */
.login-form {
  animation: fadeInUp 1s ease-out 1s both;
}

.form-group {
  margin-bottom: 25px;
}

.form-label {
  display: flex;
  align-items: center;
  color: #374151;
  font-weight: 600;
  margin-bottom: 8px;
  font-size: 0.95rem;
}

.label-icon {
  margin-right: 8px;
  color: var(--primary-blue);
  font-size: 0.9rem;
}

.login-input {
  width: 100%;
  padding: 15px 20px;
  border: 2px solid #e5e7eb;
  border-radius: var(--border-radius-sm);
  font-size: 1rem;
  transition: var(--transition);
  background-color: #ffffff;
  color: #374151;
}

.login-input:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
  transform: translateY(-2px);
}

.login-input::placeholder {
  color: #9ca3af;
}

/* Password Input Wrapper */
.password-input-wrapper {
  position: relative;
}

.password-toggle {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 5px;
  transition: var(--transition);
}

.password-toggle:hover {
  color: var(--primary-blue);
}

/* Form Options */
.form-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.remember-me {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #6b7280;
  font-size: 0.9rem;
}

.remember-me input[type="checkbox"] {
  margin-right: 8px;
  accent-color: var(--primary-blue);
}

.forgot-password {
  color: var(--primary-blue);
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 500;
  transition: var(--transition);
}

.forgot-password:hover {
  color: var(--primary-blue-dark);
  text-decoration: underline;
}

/* Login Button */
.btn-login {
  width: 100%;
  padding: 15px 20px;
  background: var(--gradient-blue);
  color: #ffffff;
  border: none;
  border-radius: var(--border-radius-sm);
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  text-decoration: none;
  margin-bottom: 30px;
}

.btn-login:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-blue-hover);
  color: #ffffff;
  text-decoration: none;
}

.btn-icon {
  font-size: 1.2rem;
}

/* Login Footer */
.login-footer {
  animation: fadeInUp 1s ease-out 1.2s both;
}

.divider {
  text-align: center;
  margin: 30px 0;
  position: relative;
}

.divider::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: #e5e7eb;
}

.divider span {
  background: #ffffff;
  padding: 0 20px;
  color: #6b7280;
  font-size: 0.9rem;
  position: relative;
}

/* Social Login */
.social-login {
  display: flex;
  gap: 15px;
  margin-bottom: 25px;
}

.social-btn {
  flex: 1;
  padding: 12px 20px;
  border: 2px solid #e5e7eb;
  border-radius: var(--border-radius-sm);
  background: #ffffff;
  color: #374151;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.social-btn:hover {
  border-color: var(--primary-blue);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.15);
}

.google-btn:hover {
  border-color: #4285f4;
  color: #4285f4;
}

.apple-btn:hover {
  border-color: #000000;
  color: #000000;
}

.signup-link {
  text-align: center;
  color: #6b7280;
  font-size: 0.9rem;
  margin: 0;
}

.signup-link a {
  color: var(--primary-blue);
  text-decoration: none;
  font-weight: 500;
}

.signup-link a:hover {
  text-decoration: underline;
}

/* Animations */
@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

/* Responsive Design */
@media only screen and (max-width: 768px) {
  .login-wrapper {
    flex-direction: column;
    max-width: 500px;
    margin: 20px;
  }
  
  .login-left-panel {
    padding: 40px 30px;
    min-height: 300px;
  }
  
  .login-right-panel {
    padding: 40px 30px;
  }
  
  .brand-title {
    font-size: 2rem;
  }
  
  .login-header h3 {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 480px) {
  .login-container {
    padding: 10px;
  }
  
  .login-wrapper {
    margin: 10px;
    border-radius: 15px;
  }
  
  .login-left-panel {
    padding: 30px 20px;
    min-height: 250px;
  }
  
  .login-right-panel {
    padding: 30px 20px;
  }
  
  .brand-logo {
    max-height: 60px;
    margin-bottom: 20px;
  }
  
  .brand-title {
    font-size: 1.8rem;
  }
  
  .brand-subtitle {
    font-size: 1rem;
  }
  
  .login-icon {
    width: 60px;
    height: 60px;
  }
  
  .login-icon svg {
    font-size: 1.5rem;
  }
  
  .login-header h3 {
    font-size: 1.3rem;
  }
  
  .social-login {
    flex-direction: column;
  }
  
  .form-options {
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
}
}
